import React, { Fragment, Component } from 'react';
import style from "../../../components/SuccessPage/success.module.css";

import CircleBtn from "../../../components/CircleBtn";
import PremiumPage from '../../../components/PremiumPage';

import api from "../../../util/api";

import gradient from 'random-gradient'

import arrow from "../../../assets/img/chevron-right.svg";
import faceLike from "../../../assets/img/recommendation/face-like.png";
import randomicon from "../../../assets/img/icon.svg";
import faceDislike from "../../../assets/img/recommendation/face-dislike.png";
import happyFace from "../../../assets/img/recommendation/happy-fave.svg";
import sadFace from "../../../assets/img/recommendation/sad-face.svg";
import getTranslation from "../../../util/translation";
import coin from '../../../assets/img/coin.svg'

class RecommendationList extends Component {

    constructor(props) {
        super(props);

        this.state = {
            recommendation: null,
            pending: true,
            rate: false,
            detailedView: false,
            answered: false,
            answers: [],
            questionCorrect: null,

        }
    }

    componentDidMount() {
        const { question } = this.props

        if (!question) {
            return false
        }

        api.get(`${question.route}/${this.props.index}`)
            .then((res) => {

                console.log(res, 'res')

                if (res.data.question) {
                    this.setState({
                        answers: res.data.question.answers
                    })
                }
            })
    }

    setRate = (rate = true) => {
        this.setState({
            rate
        })
    }

    
    renderFinalSlide = () => {
        return (<div className={style['recommendation-over']}>
                    {getTranslation('subscribeTitle', localStorage.getItem('locale'))}
                </div>)
    }
    
    renderRating = () => {
        return (
            <div className={style.rate}>
                <div className={style['rate-title']}>
                    rate this point
                </div>
                <div>
                    <a href="#" className={style['link-white']} onClick={(e) => {
                        e.preventDefault()
                        this.setRate(false)
                    }}>
                        rate later
                    </a>
                </div>
                <div className={style['rate-faces']}>
                    <button className={style['rate-face-btn']} onClick={() => {
                        this.setRate(false)
                    }}>
                        <img src={happyFace} alt=""/>
                    </button>
                    <div className={style['rate-faces-sep']}>
                        or
                    </div>
                    <button className={style['rate-face-btn']} onClick={() => {
                        this.setRate(false)
                    }}>
                        <img src={sadFace} alt=""/>
                    </button>
                </div>

                <div style={{
                    marginTop: 'auto',
                    marginBottom: '20px',
                }}>
                    <a href="#" className={style['link-pink']} onClick={(e) => {
                        e.preventDefault()

                        api.post(`recommendation/skip`, {
                            recommendationId: this.props.id
                        }).then(({data}) => {
                            this.setState({
                                recommendation: data.data,
                                pending: false
                            })
                        })


                        this.setRate(false)
                    }}>
                        skip
                    </a>
                </div>
            </div>)
    }

    renderSearchPanel = () => {
        return (
            <div>
                <div className={style.random}>
                   <img className={style.randomicon} src={randomicon} alt=""/>
                   <h2 className={style.randomtext}>
                       {getTranslation('view_popular_places', localStorage.getItem('locale'))}
                   <a href="" style={{
                    textDecoration: 'underline',
                    marginLeft: '15px'
                   }}>
                       {getTranslation('view_popular_places_link', localStorage.getItem('locale'))}
                       <img src={arrow} alt="" style={{
                    width: '10px',
                    display: 'inline',
                }} /></a></h2>
                </div> 
                <hr className={style.colored}/>
            </div> 
        )
    }

    answer = (answer) => {

       const { question } = this.props

        this.setState({
            answered: true
        })

        api.post(`${question.route}/${this.props.index}/answer`, {
            answer: answer
        }).then(({data}) => {


            this.setState({
                questionCorrect: !!data.result
            })
            document.getElementById('userCoins').innerHTML = data.coins_value

        })
    }

    getRandomInt = (min, max) => {
      min = Math.ceil(min);
      max = Math.floor(max);
      return Math.floor(Math.random() * (max - min)) + min; 
    }

    renderAnswers = (answers) => {

        let {x, y} = this.state

        return (
            <div style={{
                    position: 'absolute',
                    textAlign: 'right',
                    bottom: '30px',
                    padding: '2px 20px 2px 2px',
                    zIndex: 4,
                    margin: '0 20px',
                    borderRadius: '20px',
                    display: 'inline-grid'
                }}>
                {answers.map((answer,i) => {

                    return (
                        <button
                            key={answer}
                            onClick={() => {
                                this.answer(answer)
                            }}
                            style={{
                                width: '140px',
                                padding: '2px',
                                background: gradient(i.toString()),
                                color: 'rgb(0, 0, 0)',
                                textAlign: 'left',
                                border: '3px solid white',
                                borderRadius: '14px',
                                boxShadow: '0 0 10px -5px #000',
                                marginRight: '5px',
                                transform: `rotate(${this.getRandomInt(10,25)}deg)`,
                            }}>
                            <p style={{
                                fontSize: '8px',
                                display: 'inline-block',
                                height: '30px',
                                width: '30px',
                                textAlign: 'center',
                                margin: '0 10px 0 0',
                                lineHeight: '30px',
                                background: '#333',
                                borderRadius: '10px',
                                color: 'white',
                            }}>{i+1}</p>
                            {answer}
                        </button>
                    )
                })}
            </div>
        )
    }

    renderQuestion = (question, answered, answers) => {
        return (
                <div>
                    {!answered ?
                        <Fragment>
                            <div style={{
                                color: '#fff',
                                right: '15px',
                                position: 'absolute',
                                width: '56%',
                                textAlign: 'right',
                                top: '5px',
                                padding: '16px 20px 16px 0',
                                zIndex: 4,
                                borderRadius: '20px',
                                textDecoration: 'underline',
                                textTransform: 'uppercase'
                            }}> 
                            <p style={{
                                fontSize: '10px',
                                display: 'inline-block',
                                height: '30px',
                                padding: '0 11px',
                                width: '30px',
                                margin: '0 10px 0 0',
                                lineHeight: '30px',
                                background: '#333',
                                borderRadius: '10px',
                                color: 'white',
                            }}>?</p>
                                {question.description}
                            </div>
                            {
                                answers.length 
                                    ? this.renderAnswers(answers) 
                                    : <div>No answers</div>
                            }
                        </Fragment> : 
                        <Fragment>
                                <button
                                    onClick={() => {
                                        this.props.setCurrentRecomendation(this.props)
                                    }}
                                    style={{
                                        padding: '4px',
                                        background: '#e1e1e1',
                                        color: '#000',
                                        border: 'none',
                                        borderRadius: '4px',
                                        marginRight: '5px'
                                    }}>
                                    yes
                                </button>
                                <button
                                    onClick={() => {this.props.setCurrentSlide()}}
                                    style={{
                                        padding: '4px',
                                        background: '#e1e1e1',
                                        color: '#000',
                                        border: 'none',
                                        borderRadius: '4px',
                                        marginRight: '5px'
                                    }}>
                                    no
                                </button>
                        </Fragment>
                    }
                </div>
        )
    }

    render() {
        const { rate, pending, answered, questionCorrect, answers } = this.state
        const { question} = this.props

        if (this.props.subscribeSlide) {
            return (<div className={style['recommendation-over']}>
                {getTranslation('subscribeTitle', localStorage.getItem('locale'))}
            </div>)
        }
        const hasPhoto = !!this.props.item && !this.props.item.mainphoto && !this.props.item.mainphoto.includes('undefined')

        let borderColor = 'transparent'

        if (answered) {
            borderColor = questionCorrect ? '#238823' : '#d2222d'
        }

        return (
            <div style={{
                position: 'relative'
            }}>

                {!!question && this.renderQuestion(question,answered, answers)}

                <div className={style.wrap} style={{
                    height: this.props.categoryId === 5 ? 'calc(100vh - 60px)' : 'calc(100vh - 165px)',
                    marginTop: this.props.categoryId === 5 ? '0px' : '100px',

                    backgroundImage: hasPhoto ? `url(${this.props.item.mainphoto})` : ' url(https://image.freepik.com/free-photo/abstract-blur-hotel-interior_1203-8529.jpg)'
                }}
                     onClick={() => {
                         this.props.setCurrentRecomendation(this.props)
                     }}
                >
                    {rate && this.renderRating()}
                    {/*{this.props.categoryId === 5 && this.renderSearchPanel()}*/}

                    <div className={style.content} style={{
                        boxShadow: `1px 1px 120px 5px ${borderColor}`,
                        borderRadius: '22px',
                    }}>
                        <div className={style['content-inner']} style={{marginBottom: this.props.categoryId === 5 ? '60px' : '-40px'}}>
                            <Fragment>
                                <div className={style['content-top']} style={{
                                }}>
                                    <div className={style.col} >
                                        <div className={style.title} style={{marginTop: '-75px'}}>
                                           {getTranslation('question', localStorage.getItem('locale'))} 
                                        </div>
                                    </div>
                                    <div className={style['right-col']}>

                                    </div>
                                </div>

                                <div className={style['content-bottom']}>
                                    <div className={`${style['content-bottom-item']} ${style.cost}`}>
                                        <ul className={style['tags']} style={{display:'none'}}>
                                            {this.props.tags.map((tag,i) => {
                                                return (
                                                    <li className={style['tag']} key={i}># {tag}</li>
                                                )
                                            })}
                                        </ul>

                                        <div className={style.visited} style={{display:'none'}}>
                                            <div className={style['compare-card-photo--inner']}>
                                                {this.props.lastFiveUsersSelectedThisLocation.map((account, i) => {

                                                    return (
                                                        <div className={style['compare-card-photo--image']} key={i}>
                                                            <div className={style['avatar-img-wrap']} >
                                                                <img src={account} className={`${style['avatar-img']}`} />
                                                            </div>
                                                        </div>

                                                    )
                                                })}
                                            </div> <div style={{
                                            marginLeft: '15px'
                                        }}>
                                            +{this.props.numberOfUsersSelectedThisLocation}
                                        </div>
                                        </div>
                                    </div>
                                </div>
                            </Fragment>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default RecommendationList