import React, { Fragment, Component } from 'react';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import ReactSlider from 'react-slider'

import style from './onBoarding.module.css'

import Switch from "react-switch";
import getTranslation from "../../util/translation";

import profile3 from "../../assets/img/recommendation/profile3.svg";
import insta from "../../assets/img/insta.svg";

import qrCode from '../../assets/img/onBoarding/qrcode.gif'
import goal1 from '../../assets/img/onBoarding/goal1.gif'
import goal2 from '../../assets/img/onBoarding/goal2.gif'
import goal3 from '../../assets/img/onBoarding/goal2.gif'
import api from "../../util/api";

class OnBoarding extends Component {


    constructor(props) {
        super(props);
        let locale = localStorage.getItem('locale');
        if(!locale) {
           locale = localStorage.setItem('locale','ru')
        }
        this.state = {
            locale,
            premiumVisible: false,
            route_id: 1,
            tourName: 'test',
            name: 'test',
            age: '18',
            preferences: [],
            preferencesOptions: ['Reading', 'Hiking', 'Fishing', 'Crafts', 'Movies', 'Collecting', 'Music', 'Gardening','Video Games', 'Animals', 'Sport', 'Shopping'],
            preferencesOptions2: ['Чтение', 'Походы', 'Рыбалка', 'Рукоделие', 'Кино', 'Коллекционирование', 'Музыка', 'Сад','Игры', 'Животные', 'Спорт', 'Шопинг'],
            goal: 1,
            hunger: 100,
            adventure: 100,
            play: 100,
    }

        this.slider = React.createRef()
    }

    toggleLang = () => {
        this.setState({
            locale: this.state.locale === 'en' ? 'ru' : 'en'
        }, () => {
            localStorage.setItem('locale', this.state.locale)

        })
    }

    onLogin = () => {

        let data = {
            code: 'login',
            name: this.state.name,
            age: parseInt(this.state.age, 10),
            preference: this.state.preferences,
            target: 2,
            type_emotion: [this.state.hunger, this.state.adventure, this.state.play]
        }

        const location = new URL(window.location)

        if (location.searchParams.get('route_id')) {
            data['route_id'] = parseInt(location.searchParams.get('route_id'), 10)
        } else {
            data['route_id'] = 2
        }

        api.post('login', {...data}).then((res) => {

            this.setState({
                pending: false
            })

            if (res.data.accessToken) {
                this.props.setToken(res.data.accessToken)
            }
        }).catch(() => {
            this.setState({
                pending: false
            })

        })
    }

    // onClickPreference = (preferences,item,isActive) => {
    //      let newPreferenses = [...preferences]
    //      if (isActive) {
    //          newPreferenses = newPreferenses.filter((pref) => pref !== item)
    //      } else {
    //          newPreferenses = [...newPreferenses, item]
    //      }

    //      this.setState({
    //          preferences: newPreferenses
    //      })
    // }

    renderToggleLanguage = (locale) => {
        return (
                <div className={style.locale}>
                    <Switch
                        offColor={'#ecf0f3'}
                        onColor={'#9dabc0'}
                        onChange={this.toggleLang}
                        checked={locale === 'en'}
                        uncheckedIcon={<div className={style.locale_button2}>
                            ru
                        </div>}
                        checkedIcon={<div className={style.locale_button}>
                            en
                        </div>}
                    />
                </div>
            )
    }

    renderFirst = (locale,tourName) => {
        return (<div className={style.slide}>
                        {this.renderToggleLanguage(locale)}
                        <div className={style.premiumWrap}>
                            <button onClick={() => {
                                this.setState({
                                    premiumVisible: true
                                })
                            }}>
                                {getTranslation('onBoarding_premium', locale)}
                            </button>
                        </div>
                        <div className={style.image}>
                            <img src={qrCode} alt=""/>
                        </div>
                        <div className={style.body}>
                            <div className={style.inputWrap}>
                                <input type="text" className={style.input}
                                       value={tourName}
                                       onChange={(e) => {
                                           this.setState({
                                               tourName: e.target.value
                                           })
                                       }}
                                       placeholder={getTranslation('tourName', locale)}/>
                            </div>
                            <div className={style.dots}>
                                {[0,1,2,3].map((dot, dotIndex) => {
                                    return (
                                        <button
                                            key={dotIndex}
                                            className={`${style.dot} ${0 === dotIndex ? `${style.dotActive}` : ''}`}
                                                onClick={() => {
                                                    this.slider.current.slickGoTo(0)
                                                }}
                                        />
                                    )
                                })}
                            </div>
                            <div className={style.nextWrap}>
                                <button onClick={() => {
                                    this.slider.current.slickGoTo(1)
                                }}>
                                    {getTranslation('next', locale)}
                                </button>
                            </div>
                            <div className={style.skipWrap}>
                                <button onClick={() => this.onLogin()}>
                                    {getTranslation('skip', locale)}
                                </button>
                            </div>
                        </div>
                    </div>)
    }

    renderSecond = (locale,tourName,name,age) => {
        return (<div className={style.slide}>
                        <div className={style.body}>
                            <div className={style.selectedInterests}>
                                <h1>
                                    {getTranslation('onboarding_tour', locale)}
                                    {tourName}
                                </h1>
                            </div>
                            <div className={style.body}>
                                <div className={style.inputWrap}>
                                    <img src={profile3} alt="" style={{margin: '5px 25%', height: '8px'}}/>
                                    <input type="text" className={style.input}
                                           value={name}
                                           onChange={(e) => {
                                               this.setState({
                                                   name: e.target.value
                                               })
                                           }}
                                           placeholder={getTranslation('name', locale)}/>
                                </div>
                                <div className={style.inputWrap}>
                                    <input type="text" className={style.input}
                                           value={age}
                                           onChange={(e) => {
                                               this.setState({
                                                   age: e.target.value
                                               })
                                           }}
                                           placeholder={getTranslation('age', locale)}/>
                                </div>
                                <div style={{
                                    padding: '20px',
                                    textAlign: 'center'
                                }}>
                                    {getTranslation('or', locale)}
                                </div>
                                <div className={style.loginInstBtn}>
                                    <button onClick={this.props.onConfirm} style={{display:'flex'}}>
                                        <img src={insta} alt="" style={{height: '1.25rem', margin: '7px 12px'}}/>
                                        {getTranslation('signInInst', locale)}
                                    </button>
                                </div>
                            </div>

                            <div className={style.dots}>
                                {[0,1,2,3].map((dot, dotIndex) => {
                                    return (
                                        <button
                                            key={dotIndex}
                                            className={`${style.dot} ${1 === dotIndex ? `${style.dotActive}` : ''}`}
                                                onClick={() => {
                                                    this.slider.current.slickGoTo(1)
                                                }}
                                        />
                                    )
                                })}
                            </div>
                            <div className={style.nextWrap}>
                                <button onClick={() => {
                                    this.slider.current.slickGoTo(2)
                                }}>
                                    {getTranslation('next', locale)}
                                </button>
                            </div>
                            <div className={style.skipWrap}>
                                <button onClick={this.props.onConfirm}>
                                    {getTranslation('skip', locale)}
                                </button>
                            </div>
                        </div>
                    </div>)
    }

    renderThird = (locale,name,preferences,preferencesOptions) => {
        return (<div className={style.slide}>
                    <div className={style.body}>
                        <div className={style.selectedInterests}>
                            <h1>
                                {getTranslation('preferences_hello', locale)}
                                &nbsp;
                                {name}
                            </h1>
                        </div>
                        <div className={style.selectedInterests}>
                            {getTranslation('selectedInterests', locale)}
                            <div style={{
                                marginLeft: '20px'
                            }}>
                                {preferences.length}
                            </div>

                        </div>
                        <div className={style.interestsGrid}>
                            {preferencesOptions.map((item) => {

                                const isActive = preferences.includes(item)

                                return (
                                    <div key={item}
                                         onClick={()=>{
                                                     let newPreferenses = [...preferences]
         if (isActive) {
             newPreferenses = newPreferenses.filter((pref) => pref !== item)
         } else {
             newPreferenses = [...newPreferenses, item]
         }

         this.setState({
             preferences: newPreferenses
         })
                                         }}
                                         className={`${style.interestsGridItem} ${isActive ? style.interestsGridItemActive : ''}`}>
                                        <div className={style.interestsGridItemText}>
                                            {item}
                                        </div>
                                    </div>
                                )
                            })}
                        </div>
                        <div className={style.dots}>
                            {[0,1,2,3].map((dot, dotIndex) => {
                                return (
                                    <button key={dotIndex}
                                        className={`${style.dot} ${2 === dotIndex ? `${style.dotActive}` : ''}`}
                                            onClick={() => {
                                                this.slider.current.slickGoTo(2)
                                            }}
                                    />
                                )
                            })}
                        </div>
                        <div className={style.nextWrap}>
                            <button onClick={() => {
                                this.slider.current.slickGoTo(3)
                            }}>
                                {getTranslation('next', locale)}
                            </button>
                        </div>
                        <div className={style.skipWrap}>
                            <button onClick={this.props.onConfirm}>
                                {getTranslation('skip', locale)}
                            </button>
                        </div>
                    </div>
                </div>)
    }

    renderForth = (locale) => {

        const settings = {
            infinite: false,
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1,
            arrows: true
        }

        return (<div className={style.slide}>
                    <div className={style.body} style={{marginTop: '40px'}}>
                        <div className={style.sliderWrap}>
                            <div className={style.inputLabel}>
                                {getTranslation('goal', locale)}
                            </div>
                            <Slider {...settings} ref={this.slider} className={style.goalPanel}>
                                <div className={style.goalSlide}><img src={goal1} width={220} style={{margin: '0 auto'}} alt=""/></div>
                                <div className={style.goalSlide}><img src={goal2} width={220} style={{margin: '0 auto'}} alt=""/></div>
                                <div className={style.goalSlide}><img src={goal3} width={220} style={{margin: '0 auto'}} alt=""/></div>
                            </Slider>
                        </div>
                        <div className={style.sliderWrap}>
                            <div className={style.inputLabel}>
                                {getTranslation('emotion1', locale)}
                            </div>
                            <ReactSlider
                                defaultValue={100}
                                className="horizontal-slider"
                                thumbClassName="example-thumb"
                                trackClassName="example-track"
                                onChange={val => {
                                    this.setState({
                                        hunger: val
                                    })
                                }}
                                renderThumb={(props, state) => <div 
                                        {...props}>
                                        <div className="slider-count">{state.valueNow}</div>
                                    </div>}
                            />
                        </div>
                        <div className={style.sliderWrap}>
                            <div className={style.inputLabel}>
                                {getTranslation('emotion2', locale)}
                            </div>
                            <ReactSlider
                                defaultValue={100}
                                className="horizontal-slider"
                                thumbClassName="example-thumb"
                                trackClassName="example-track"
                                onChange={val => {
                                    this.setState({
                                        adventure: val
                                    })
                                }}
                                renderThumb={(props, state) => <div 
                                        {...props}>
                                        <div className="slider-count">{state.valueNow}</div>
                                    </div>}
                            />
                        </div>
                        <div className={style.sliderWrap}>
                            <div className={style.inputLabel}>
                                {getTranslation('emotion3', locale)}
                            </div>
                            <ReactSlider
                                defaultValue={100}
                                className="horizontal-slider"
                                thumbClassName="example-thumb"
                                trackClassName="example-track"
                                onChange={val => {
                                    this.setState({
                                        play: val
                                    })
                                }}
                                renderThumb={(props, state) => <div 
                                        {...props}>
                                        <div className="slider-count">{state.valueNow}</div>
                                    </div>}
                            />
                        </div>
                        <div className={style.dots}>
                            {[0,1,2,3].map((dot, dotIndex) => {
                                return (
                                    <button key={dotIndex}
                                        className={`${style.dot} ${3 === dotIndex ? `${style.dotActive}` : ''}`}
                                            onClick={() => {
                                                this.slider.current.slickGoTo(3)
                                            }}
                                    />
                                )
                            })}
                        </div>
                        <div className={style.nextWrap}>
                            <button onClick={() => {
                                this.onLogin()
                            }}>
                                {getTranslation('getStarted', locale)}
                            </button>
                        </div>
                    </div>
                </div>)
    }

    renderPremium = (locale) => {
        return (
                <div className={style.premiumVisible}>
                    <div className={style.premiumVisibleContent}>
                        <div className={style.premiumBody}>
                            <div className={style.premiumBodyItem}>
                                <div className={style.premiumBodyItemTitle}>
                                    {getTranslation('premiumTitle1', locale)}
                                </div>
                                <div className={style.premiumBodyItemText}>
                                    {getTranslation('premiumText1', locale)}
                                </div>
                            </div>
                            <div className={style.premiumBodyItem}>
                                <div className={style.premiumBodyItemTitle}>
                                    {getTranslation('premiumTitle2', locale)}
                                </div>
                                <div className={style.premiumBodyItemText}>
                                    {getTranslation('premiumText2', locale)}
                                </div>
                            </div>
                            <div className={style.premiumBodyItem}>
                                <div className={style.premiumBodyItemTitle}>
                                    {getTranslation('premiumTitle3', locale)}
                                </div>
                                <div className={style.premiumBodyItemText}>
                                    {getTranslation('premiumText3', locale)}
                                </div>
                            </div>
                            <div className={style.premiumBodyItem}>
                                <div className={style.premiumBodyItemTitle}>
                                    {getTranslation('premiumTitle4', locale)}
                                </div>
                                <div className={style.premiumBodyItemText}>
                                    {getTranslation('premiumText4', locale)}
                                </div>
                            </div>
                        </div>
                        <div className={style.premiumBodyItemTitle}>
                            {getTranslation('tryTitle', locale)}
                        </div>
                        <div className={style.premiumBodyItemText}>
                            {getTranslation('tryText', locale)}
                        </div>
                        <div className={style.buyButton}>
                            <button>
                                {getTranslation('tryButton', locale)}
                            </button>
                        </div>
                        <div className={style.cancelPremiumView}>
                            <button onClick={() => {
                                this.setState({
                                    premiumVisible: false
                                })
                            }}>
                                {getTranslation('tryLater', locale)}
                            </button>
                        </div>
                    </div>
                </div>
        )
    }

    render() {

        const { locale, premiumVisible, preferences, preferencesOptions, preferencesOptions2, name, age, tourName } = this.state

        const settings = {
            dots: false,
            infinite: false,
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1,
            arrows: false
        }

        if (premiumVisible) {
            this.renderPremium(locale)
        }

        let preferencesData = locale == 'en' ? preferencesOptions : preferencesOptions2

        return (
                <Slider {...settings} ref={this.slider}>
                    {this.renderFirst(locale,tourName)}
                    {this.renderSecond(locale,tourName,name,age)}
                    {this.renderThird(locale,name,preferences,preferencesData)}
                    {this.renderForth(locale)}
                </Slider>
        )

    }


}

export default OnBoarding